header{
    // border: 1px solid black;
    height: 150px;
    width: 100%;

    .ulList{
        display: none;
    }

    .head-top{
        border: 1px solid rgb(233, 233, 233);
        padding: 4px;
        width: 100%;

        .center{
            width: 70%;
            margin: auto;
            display: flex;
            justify-content: space-between;
        }




        .ul-parent{
            display: flex;
            // border: 1px solid black;

            width: 52%;
            justify-content: space-around;
            align-items: center;

            ul{

            display: flex;
            padding: 10px;
            

            }
            ul li{
                
                list-style: none;
                display: flex;
                    // border: 1px solid black;
                    justify-content: space-between;
                    margin-left:14px;

                
            }
            
            ul a{
                
                text-decoration: none;
                font-size: 12px;
                
            }

            
            button{
                border: none;
                outline: none;
                background-color: whitesmoke;
                margin-left: 10px;
                margin-right: 20px;
                padding: 8px;
                border-radius: 25px;

                a{
                    padding: 10px;
                }
                
                
            }



        }

        
    }


    .header-bottom{

        width: 70%;
        margin: auto;
        display: flex;
        justify-content: space-between;
        align-items: center;
        height: 55%;

        .img{
            // border: 1px solid black;
            background-color: blue;
            padding: 10px;
            border-radius: 4px;
            img{
                height: 20px;
            }
            
         
        }

        ul {
            display: flex;
            width: 60%;
            justify-content: flex-end;
            align-items: center;
            padding: 20px;
        
            li{
                list-style: none;
                padding-left: 40px;
            }

            li a{
                text-decoration: none;
            }
        
        }

        


    }

    .burger{
        display: none;
    }

    // .ulList{
    //     display: none;
    // }

    position: relative;


  
}


@media screen and  (max-width : 786px){

    .ulList{

        background-color: whitesmoke;
        width: 100%;
        display: block !important;

        position: absolute;
            top: 50%;
    height: 40vh;

    .links-mobile{
            display: flex;
    // border: 1px solid;
    height: 100%;
    flex-direction: column;
    justify-content: space-around;
    align-items: start;
    padding: 40px;

    li{
        list-style: none;
        a{
            text-decoration: none;
        }
    }
    }
    }

}

// media Query
@media screen and  (max-width : 786px){

    header{
        display: flex;
        height: 135px;


        
    }

    .head-top{
        display: none;
    }

    .header-bottom .links{
        display: none;
    }

    .burger{
        width: 40px;
        text-align: center;
        background-color: rgb(216, 211, 211);
        display: block !important;
       
        img{
            margin-top: 5px;
        }
    }


    header .header-bottom {
        width: 80%;
    
    
    }

    


    
}




    




